/**
 * Here we add our toolkit specific components and override default variables.
 */

/**
 * Import Variables
 */

@import "toolkit/vars"; //Custom vars that override bootstrap vars

//Plugins
@import "plugins/tablesaw";
@import "plugins/multiple-select";
@import "plugins/idangerous-swiper";
@import "plugins/fullcalendar";
@import "plugins/owl-carousel";
@import "toolkit/owl-carousel-custom"; //custom owl swiper
@import "plugins/prism"; // syntax highlighting
@import "plugins/ekko-lightbox"; // lightbox

//Boostrap extends & overwrites
@import "toolkit/bootstrap-extends";
@import "toolkit/modal-iframe";

@import "toolkit/mixins-skin"; //skin applied to various elements

//Core Toolkit
@import "toolkit/tk-mixins"; 
@import "toolkit/tk-utilities"; //refactor

//Elements styles
@import "toolkit/typography";
@import "toolkit/forms";
@import "toolkit/tables";

//Icons (TEUD DEUX: Make a third party plugin)
@import "toolkit/tk-icons"; //custom toolkit icons, file generated by gulp
@import "toolkit/tk-icons-helper"; //css for better gulp icons, wont be need when icons become a plugin

//Layout
@import "toolkit/layout"; //site containers and wrappers

//Large components that make up the page layout and structure
@import "toolkit/masthead";
@import "toolkit/quicklinks";
@import "toolkit/local-header";
@import "toolkit/site-search";
@import "toolkit/quick-search"; //ajaxdropdown
@import "toolkit/site-footer";
@import "toolkit/columns"; //page column + sidebar
@import "toolkit/sidebar";

//Nav
@import "toolkit/nav-priority";
@import "toolkit/nav-multiblock"; //breaks the BS grid
@import "toolkit/navicon";

//Components (keep alphabetical)

@import "toolkit/banner"; //hero banner NB not the swiper
@import "toolkit/breadcrumb"; //modify BS breadcrumb
@import "toolkit/cards"; //card pattern, flat or stacked
@import "toolkit/dividers"; // header divider and hr with an image
@import "toolkit/expander"; 
@import "toolkit/flags"; 
@import "toolkit/images"; //image ration and responsive images
@import "toolkit/islands"; //boxed in content
@import "toolkit/lists"; //styled lists and nav
@import "toolkit/news-events"; // news and events widget
@import "toolkit/page-contents"; //page contents list
@import "toolkit/page-footer"; //page footer, next & prev
@import "toolkit/pagination"; //modify BS version
@import "toolkit/profile-block";
@import "toolkit/responsive-accordion";
@import "toolkit/social-share"; //social sharing buttons
@import "toolkit/swiper"; //old idangerous swiper - remove when possible

@import "toolkit/tabs"; //custom tabs using BS JS
@import "toolkit/twitter-feed"; //twitter footer and widget

//Skin
@import "toolkit/skin"; //skin applied to various elements

//Toolkit docs
@import "toolkit/docs";

//Shameful
@import "toolkit/shame";
